/* Optional enhancements if you want */
.rounded-circle {
  object-fit: cover;
}

a i {
  transition: transform 0.2s ease;
}

a:hover i {
  transform: scale(1.2);
  color: #17a2b8;
}

.icon-group {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.icon-group a {
  font-size: 1.25rem;
  color: #333;
  transition: transform 0.2s ease, color 0.2s ease;
}

.icon-group a:hover {
  color: #0077b5; /* LinkedIn blue */
  transform: scale(1.2);
}

@media (max-width: 768px) {
  h1, h2 {
    font-size: 1.8rem;
    text-align: center;
  }

  .team-card {
    padding: 1rem;
  }

  .icon-group {
    gap: 1rem;
  }

  .navbar-brand {
    font-size: 1.2rem;
  }
}
